<template>
  <div class="change-email">
    <!--  Header  -->
    <mt-header fixed title="">
      <span slot="left" @click="goBack">
        <mt-button icon="back"></mt-button>
      </span>
    </mt-header>
    <div class="options-content">
      <yd-cell-item>
        <span slot="left"></span>
        <yd-input slot="right" required :debug="true" regex="email" placeholder="" v-model="email" ref="emailInput"></yd-input>
      <!--<yd-input  slot="right" required :debug="true" regex="mobile" placeholder="" v-model="phone" ref="phoneInput"></yd-input> -->
      </yd-cell-item>
      <yd-cell-item>
        <span slot="left"></span>
        <yd-input slot="right" placeholder="" v-model="code"></yd-input>
         <!--<yd-input slot="right" placeholder="" v-model="code"></yd-inputyd-input> -->
        <yd-sendcode
          slot="right"
          @click.native="sendCode"
          v-model="start"
          type="warning"
          init-str=""
          second="60"
          run-str="{%s}"
        ></yd-sendcode>
      </yd-cell-item>
    </div>
    <div class="post-btn"><mt-button type="danger" size="large" @click="editEmail"></mt-button></div>
  </div>
</template>

<script>
  import {getEditEmailCode, emailChange, getEditPhoneCode, phoneChange} from './../../../../api/index';
  import { MessageBox } from 'mint-ui';
  import {mapState} from 'vuex';
  import {mapActions} from 'vuex'

  export default {
    name: "ChangeEmail",
    data() {
      return {
        email: '',
        phone: '',
        code: null,
        start: false,  // 
      }
    },
    computed:{
      ...mapState(['userInfo'])
    },
    methods: {
      ...mapActions(['syncUserInfo']), // 
      goBack() {
        // 
        this.$router.go(-1);
      },
      async sendCode() {
        if(this.$refs.emailInput.valid){
          // 
       // if(this.$refs.phoneInput.valid){
          // 
          this.$dialog.loading.open('...');
          const result = await getEditEmailCode(this.email);
        //  const result = await getEditPhoneCode(this.phone);
          console.log(result);
          this.start = true;
          this.$dialog.loading.close();
          if(result.error_code === 0){
            this.$dialog.toast({
              mes: '',
              icon: 'success',
              timeout: 1000
            });
          }else if(result.error_code === 2001){
            this.$dialog.toast({
              mes: result.error_msg,
              icon: 'error',
              timeout: 1000
            });
            this.start = false;
          }
        }else{
          // /
          this.$dialog.toast({
            mes: '' + this.$refs.emailInput.errorMsg,
           // mes: '' + this.$refs.phoneInput.errorMsg,
            icon: 'error',
            timeout: 1000
          });
        }
      },
      async editEmail(){
        if(this.$refs.emailInput.valid && this.code){
       //  if(this.$refs.phoneInput.valid && this.code){
          this.code = Number(this.code);
          const result = await emailChange(this.email, this.code);
         // const result = await phoneChange(this.phone, this.code);
          if(result.error_code === 0){
            this.$dialog.toast({
              mes: '!',
              icon: 'success',
              timeout: 1000
            });
            this.userInfo.email = this.email;
          //  this.userInfo.phone = this.phone;
            this.syncUserInfo(this.userInfo);
            setTimeout(()=>{
              this.$router.replace('/me');
            }, 1000);
            this.start = false;
            this.email = '';
            this.phone = '';
            this.code = null;
          }else{
            this.$dialog.toast({
              mes: result.error_msg,
              icon: 'error',
              timeout: 1000
            });
          }
        }else{
          this.$dialog.toast({
            mes: '',
            icon: 'error',
            timeout: 1000
          });
        }
      },
    }
  }
</script>

<style scoped lang="less" ref="stylesheet/less">
  .change-email{
    background: #f5f5f5;
    width: 100%;
    height: 100%;
    .mint-header {
      z-index: 99;
       background: #f5f5f5;
       height: 40px;
       color: #000;
       font-weight: bolder;
       font-size: 18px;
       border-bottom: 1px solid #D4D4D4;
    }
    .options-content{
      padding-top: 40%;
      .yd-cell-item{
        width: 80%;
        margin: 0 auto 10px;
        border: 1px solid rgba(2, 167, 240, 0.505882352941176);
        border-radius: 10px;
        background: #fff;
      }
    }
    .post-btn{
      text-align: center;
      padding: 50px 35px;
      width: 100%;
    }
  }
</style>



